import React, { Component } from "react";
import less from "./style.less";
import Button from "components/app/button/articleButton";
import { connect } from "react-redux";
import { LIKE_ARTICLE } from "GQL/Mutation";
import PropTypes from "prop-types";

class ToolBox extends Component {
  constructor (props) {
    super(props);
  }

  render () {
    const { userState: { isLogin }, likeNumber } = this.props;
    return (
      <div className={ less["tool-box"] }>
        {
          isLogin ? <ul>
            <li>
              <Button currentNumber={ likeNumber }
                      disabled={ false }
                      icon="fa fa-thumbs-up"
                      onClick={ () => this.setState(state => ({ number: state.number + 1 })) }

              />
            </li>
            <li>
              <Button currentNumber={ this.state.number }
                      disabled={ false }
                      icon="fa fa-star"
                      onClick={ () => this.setState(state => ({ number: state.number + 1 })) }
                      style={ "style2" }
              />
            </li>
            <li>
              <Button currentNumber={ this.state.number }
                      disabled={ false }
                      icon="fa fa-comment-alt"
                      onClick={ () => this.setState(state => ({ number: state.number + 1 })) }
                      style={ "style3" }
              />
            </li>
            <li className={ less.share }>
              SHARE
            </li>
            <li>
              <Button currentNumber={ this.state.number }
                      disabled={ false }
                      icon="fa fa-share-square"
                      onClick={ () => this.setState(state => ({ number: state.number + 1 })) }
                      style={ "style4" }
                      noTip
              />
            </li>
            <li>
              <Button currentNumber={ this.state.number }
                      disabled={ false }
                      icon="fa fa-question"
                      onClick={ () => this.setState(state => ({ number: state.number + 1 })) }
                      style={ "style5" }
                      noTip
              />
            </li>
          </ul> : ""
        }
      </div>
    );
  }
}

ToolBox.propTypes = {
  userState: PropTypes.object,
  likeNumber: PropTypes.number,
  
};
export default connect(state => ({
  userState: state.user
}))(ToolBox)